@use '../base/variables' as v;
@use '../base/mixins' as m;


.favorite {
    margin-top: 5rem;
    background-color: v.$white;
    box-shadow: 0px 0px 15px 3px #c2c2c2;
    border-radius: 1.5rem;

    &__button {
        background-color: v.$orange;
        color: v.$white;
        padding: 1.3rem 3rem;
        border-radius: 1rem;
        border: none;
        cursor: pointer;
        text-align: center;
        margin: 2rem 0;
        width: 85%;

        @include m.tablet {
            width: 60%;
        }

        @include m.desktop {
            margin: 2rem 1rem;
        }
    }


    &__grid {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        @include m.desktop {
            flex-direction: row;
            padding: 1.5rem;
        }
    }



    &__header {
        color: v.$orange;
        font-size: 2.2rem;
        font-weight: 300;
        text-align: left;
        padding: 1.5rem 0;

        @include m.desktop {
            padding: 2rem;
        }
    }

    &__time {
        color: v.$blue;
        font-size: 1.6rem;
        padding-bottom: 1.5rem;
        margin: 0;
        display: flex;

        @include m.desktop {
            padding: 0 0 2rem 2rem;
        }


        &::before {
            content: url("../../src/img/time.svg");
            display: flex;
            width: 2.3rem;
            height: 0;
        }
    }


    &__svg {
        display: flex;
        justify-content: flex-end;
        padding: 1.5rem;
        color: v.$blue;


    }
}