@use '../base/variables' as v;
@use '../base/mixins' as m;






.profile {


    background-image: linear-gradient(to top, v.$secondary 0%, v.$primary 100%);
    position: relative;



    &__grid {

        display: flex;
        padding: 2rem 0 20rem 1rem;

        @include m.tablet {

            display: flex;
        }
    }


    &__svg {
        color: v.$white;
        padding-right: 0.5rem;
        padding-top: 0.2rem;
    }

    &__imagen {
        width: 5rem;
        height: 5rem;
        margin-block-start: auto;

        @include m.tablet {
            width: 6rem;
            height: 6rem;
            margin-inline-start: auto;
            cursor: pointer;
        }
    }


}



.picture {

    &__link {
        color: v.$primary;
        display: flex;
        justify-content: center;

    }

    &__img {
        margin: 0 auto;
        margin-top: -10rem;
        z-index: 1;
        position: relative;
        width: 18rem;


        @include m.tablet {
            width: 25rem;
        }
    }


}



.information {

    padding-top: 5rem;
    width: 90%;

    @include m.tablet {
        width: 70%;
    }

    @include m.desktop {
        width: 50%;
    }

    @include m.hd {
        width: 25%;
    }

    &__name {
        color: v.$primary;
    }

    &__lastname {
        padding-top: 2rem;
        color: v.$primary;
    }


    h5 {
        font-weight: 100;
        margin: 0;
    }

    h4 {
        margin: 1rem 0 0 0;
        font-size: 2rem;
        font-weight: 200;
    }


    &__separation {
        border: 0 none v.$primary;
        border-top: 1px solid v.$primary;
        height: 1px;
        margin: 2.5rem 0;
        display: block;
        clear: both;
        opacity: 50%;
    }


    &__favorites {
        color: v.$primary;
        display: flex;
        align-items: center;

    }

    svg {
        margin-top: 1rem;
        margin-right: 1rem;
    }


    p {
        color: v.$primary;
        margin-left: auto;
        font-size: 2rem;
        margin-top: 3rem;
    }


    &__email {
        color: v.$primary;
        display: flex;
        align-items: center;

    }

    &__password {
        color: v.$primary;
        display: flex;
        align-items: center;

    }



    &__delete {
        color: v.$orange;
        padding: 6rem 0 4rem 0;
        font-weight: 500;


        @include m.tablet {
            padding-top: 10rem;
        }
    }
}