@use '../base/variables' as v;
@use '../base/mixins' as m;


.contact {


    &__logo {
        margin: 0 auto;
        padding: 5rem 0;
        width: 25%;

        @include m.tablet {
            width: 20%;
        }

        @include m.desktop {
            width: 10%;
        }


        @include m.hd {
            width: 8%;
        }
    }


    &__header {
        text-align: center;
        color: v.$blue;
        font-weight: 100;
        padding: 6rem 0 6rem 0;

        @include m.tablet {
            padding: 12rem 0 6rem 0;
        }

        @include m.desktop {
            padding: 8rem 0 6rem 0;
        }

        @include m.hd {
            padding: 16rem 0 6rem 0;
        }
    }


    &__grid {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: v.$white;
        box-shadow: 0px 0px 15px 3px #c2c2c2;
        border-radius: 1.5rem;
        margin: 2rem;
        padding: 4rem 2rem;
    }



    &__span {
        color: v.$white;
        font-size: 1.4rem;
        display: flex;
        justify-content: flex-end;
        width: 70%;
        margin-bottom: 3rem;
        margin-top: 1rem;
        cursor: pointer;

        @include m.tablet {
            width: 50%;
        }

        @include m.desktop {
            width: 30%;
        }

    }

    &__button-login {
        background-color: v.$secondary;
        color: v.$white;
        padding: 1.3rem 0;
        border-radius: 1rem;
        width: 70%;
        border: 0.5px solid #FFFFFF;
        cursor: pointer;


        @include m.tablet {
            width: 50%;
        }

        @include m.desktop {
            width: 30%;
        }
    }


    input {
        padding: 1.3rem 0 1.3rem 1rem;
        border-radius: 0.6rem;
        width: 100%;
        border: 0.5px solid #acacac;
        margin-bottom: 2rem;
        font-size: 1.1rem;
        font-weight: 100;


        &::placeholder {
            color: v.$blue;
            opacity: 70%;
        }

    }


    &__button-send {
        background-color: v.$orange;
        color: v.$white;
        padding: 1.3rem 0;
        border-radius: 1rem;
        border: none;
        width: 80%;
        cursor: pointer;
        margin-top: 3rem;

        @include m.tablet {
            width: 60%;
        }

        @include m.desktop {
            width: 50%;
        }
    }

    textarea {

        padding: 1.3rem 0 1.3rem 1rem;
        border-radius: 0.6rem;
        width: 100%;
        border: 0.5px solid #acacac;
        margin-bottom: 2rem;
        font-size: 1.1rem;
        font-weight: 100;
        max-width: -webkit-fill-available;
        max-height: 10rem;

        @include m.tablet {

            max-height: 15rem;
        }

        &::placeholder {
            color: v.$blue;
            opacity: 70%;
        }
    }


    form {
        width: 100%;
    }
}