@use '../base/variables' as v;
@use '../base/mixins' as m;

.bg {
    background-image: linear-gradient(to top, v.$secondary 0%, v.$primary 100%);
    background-attachment: fixed;
}

.register {


    &__logo {
        margin: 0 auto;
        padding: 5rem 0;
        width: 25%;

        @include m.tablet {
            width: 20%;
        }

        @include m.desktop {
            width: 10%;
        }


        @include m.hd {
            width: 8%;
        }
    }


    &__header {
        text-align: center;
        color: v.$blue;
        font-weight: 100;
        padding: 6rem 0 6rem 0;

        @include m.tablet {
            padding: 12rem 0 6rem 0;
        }

        @include m.desktop {
            padding: 8rem 0 6rem 0;
        }

        @include m.hd {
            padding: 16rem 0 6rem 0;
        }
    }


    &__grid {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: inherit;
    }



    &__span {
        color: v.$white;
        font-size: 1.4rem;
        display: flex;
        justify-content: flex-end;
        width: 70%;
        margin-bottom: 3rem;
        margin-top: 1rem;
        cursor: pointer;

        @include m.tablet {
            width: 50%;
        }

        @include m.desktop {
            width: 30%;
        }

    }

    &__button-login {
        background-color: v.$secondary;
        color: v.$white;
        padding: 1.3rem 0;
        border-radius: 1rem;
        width: 70%;
        border: 0.5px solid #FFFFFF;
        cursor: pointer;


        @include m.tablet {
            width: 50%;
        }

        @include m.desktop {
            width: 30%;
        }
    }


    input {
        padding: 1.3rem 0 1.3rem 1rem;
        border-radius: 0.6rem;
        width: 70%;
        border: none;
        margin-bottom: 2rem;
        font-size: 1.1rem;
        font-weight: 100;
        width: 70%;


        &:nth-of-type(5) {
            margin-bottom: 3rem;
        }

        @include m.tablet {
            width: 50%;
        }

        @include m.desktop {
            width: 30%;
        }

        @include m.hd {
            width: 20%;
        }

        &::placeholder {
            color: v.$blue;
            opacity: 70%;
        }

    }


    &__button-register {
        background-color: v.$primary;
        padding: 1.3rem 0;
        border-radius: 1rem;
        border: none;
        width: 70%;
        cursor: pointer;

        a {
            color: v.$white;
        }


        @include m.tablet {
            width: 50%;
        }

        @include m.desktop {
            width: 30%;
            margin-bottom: 4rem;
        }

        @include m.hd {
            width: 20%;
        }
    }
}