@use '../base/variables' as v;
@use '../base/mixins' as m;



#menu-hamburguesa {
    // background-image: linear-gradient(to top, v.$secondary 0%, v.$primary 100%);
    display: flex;
    justify-content: flex-start;

    @include m.tablet {
        display: none;
        justify-content: flex-end;
        width: 100%;
        text-align: center;
    }
}

#menu-hamburguesa ul li {
    padding: 1.5rem 0;
}

#menu-hamburguesa ul li a {
    color: v.$blue;
    font-size: 1.8rem;
    font-family: sans-serif;
}

li {
    list-style-type: none;
    text-align: left;
}

#boton-hamburguesa {
    background-color: inherit;
    font-size: 4rem;
    font-family: sans-serif;
    border: none;
    cursor: pointer;
    display: flex;
    margin-inline-start: auto;


}




/* Transición suave al desplegar el menú */
#menu-hamburguesa {
    /* ...otros estilos... */
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease-in-out;
}

#menu-hamburguesa.mostrando {
    max-height: 50rem;
    width: 100%;
    /* Ajustar este valor según la altura máxima del menú */
}



/* Rotar el icono de la hamburguesa al abrir el menú */
#boton-hamburguesa span.rotar {
    transform: rotate(90deg);
}



/* Estilos del menú horizontal para pantallas grandes */
@include m.tablet {
    #boton-hamburguesa {
        display: none;
    }

    #menu-hamburguesa {
        display: block;
        max-height: initial;
        overflow: initial;
    }

    #menu-hamburguesa ul {
        display: flex;
        justify-content: right;
        margin: 0;
        align-items: center;
    }

    #menu-hamburguesa:first-of-type {
        justify-content: flex-end;
    }

    #menu-hamburguesa ul li {
        padding: 3rem 2rem;
    }

    #boton-hamburguesa {

        background-image: linear-gradient(to top, v.$secondary 0%, v.$primary 100%);
        color: v.$white;
        font-size: 2.4re;
        font-family: sans-serif;

    }
}



.menu {


    &__grid {
        background-image: linear-gradient(to top, v.$secondary 0%, v.$primary 100%);

        @include m.tablet {

            display: flex;
        }
    }

    &__imagen {
        width: 5rem;
        height: 5rem;
        margin-block-start: auto;

        @include m.tablet {
            width: 6rem;
            height: 6rem;
            margin-inline-start: auto;
            cursor: pointer;
        }
    }



    &__logox2 {
        display: none;



        @include m.tablet {
            display: flex;
            width: 25%;
            padding: auto auto auto 2rem;
            margin-left: 2rem;

        }

        @include m.desktop {
            width: 15%;

        }

        @include m.hd {
            width: 8%;

        }
    }
}


.step {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    align-items: flex-start;


    &__back {
        display: flex;
        padding-left: 1.8rem;
        padding-top: 3rem;
    }

    &__svg {
        color: v.$primary;
        padding-right: 0.5rem;
        padding-top: 0.2rem;


    }

    svg {
        @include m.desktop {
            width: 1.8rem;
            height: 1.8rem;
        }
    }

    &__link {
        color: v.$primary;

        @include m.desktop {
            font-size: 1.8rem;
        }
    }





    &__img {
        padding: 3rem 0 0 0;

    }




}



.steps {

    margin-top: 2rem;

    &__button {
        background-color: v.$orange;
        color: v.$white;
        padding: 1.3rem 3rem;
        border-radius: 1rem;
        border: none;
        cursor: pointer;
        text-align: center;
        margin: 2rem 0;
        width: 85%;

        @include m.tablet {
            width: 60%;
        }

        @include m.desktop {
            margin: 2rem 1rem;
        }
    }


    &__grid {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: baseline;

        @include m.desktop {
            flex-direction: row;

        }
    }

    &__svg {
        color: v.$blue;

        svg {
            @include m.tablet {
                width: 2.2rem;
                height: 2.2rem;
            }


            @include m.desktop {
                width: 2.5rem;
                height: 2.5rem;
            }

            @include m.hd {
                width: 2.7rem;
                height: 2.7rem;
            }
        }


        &:hover {
            content: url("/src/img/heart-fill.svg");
            color: v.$blue;

            @include m.tablet {
                width: 2.2rem;
                height: 2.2rem;
            }


            @include m.desktop {
                width: 2.5rem;
                height: 2.5rem;
            }

            @include m.hd {
                width: 2.7rem;
                height: 2.7rem;
            }
        }
    }

    &__header {
        color: v.$orange;
        font-size: 2.2rem;
        font-weight: 300;
        text-align: left;
        padding: 1.5rem 0;


    }

    &__time {
        color: v.$blue;
        font-size: 1.6rem;
        padding-bottom: 1.5rem;
        margin: 0;
        display: flex;


        &::before {
            content: url("../../src/img/time.svg");
            display: flex;
            width: 2.3rem;
            height: 0;
        }
    }
}

.ingredientes {

    h3 {
        font-weight: 200;
        margin-top: 0;
        margin-bottom: 0.5rem;
        font-size: 1.8rem;

        &:first-of-type {
            margin-top: 2rem;
        }
    }
}



.pasos {
    margin-top: 4rem;
    font-weight: 200;

    &__step {

        h2 {
            color: v.$primary;
            font-size: 1.7rem;
        }
    }

    p {
        color: v.$black;
        margin-top: 0.5rem;
    }
}