@use '../base/variables' as v;
@use '../base/mixins' as m;

.bg {
    background-image: linear-gradient(to top, v.$secondary 0%, v.$primary 100%);
    background-attachment: fixed;
}

.login {


    &__logo {
        margin: 0 auto;
        padding: 5rem 0;
        width: 25%;

        @include m.tablet {
            width: 20%;
        }

        @include m.desktop {
            width: 10%;
        }


        @include m.hd {
            width: 8%;
        }
    }


    &__header {
        text-align: center;
        color: v.$blue;
        font-weight: 100;
        padding: 8rem 0 6rem 0;

        @include m.tablet {
            padding: 15rem 0 6rem 0;
        }

        @include m.desktop {
            padding: 10rem 0 6rem 0;
        }

        @include m.hd {
            padding: 20rem 0 6rem 0;
        }
    }


    &__grid {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: inherit;
    }



    &__span {

        font-size: 1.4rem;
        display: flex;
        justify-content: flex-end;
        width: 70%;
        margin-bottom: 3rem;
        margin-top: 1rem;
        cursor: pointer;

        a {
            color: v.$black;
        }

        @include m.tablet {
            width: 50%;
        }

        @include m.desktop {
            width: 30%;
        }

    }

    &__button-login {
        background-color: v.$secondary;
        padding: 1.3rem 0;
        border-radius: 1rem;
        width: 70%;
        border: none;
        cursor: pointer;


        a {
            color: v.$black;
        }

        @include m.tablet {
            width: 50%;
        }

        @include m.desktop {
            width: 30%;
        }
    }


    &__button-send {
        background-color: v.$primary;
        color: v.$white;
        padding: 1.3rem 0;
        border-radius: 1rem;
        border: none;
        width: 80%;
        cursor: pointer;
        margin: 0 auto;
        display: flex;
        justify-content: center;

        @include m.tablet {
            width: 50%;
        }

        @include m.desktop {
            width: 40%;
        }

        @include m.hd {
            width: 25%;
        }
    }

    input {
        padding: 1.3rem 0 1.3rem 1rem;
        border-radius: 0.6rem;
        width: 70%;
        border: none;
        margin-bottom: 2rem;
        font-size: 1.1rem;
        font-weight: 100;
        width: 70%;


        &:nth-of-type(2) {
            margin-bottom: 0;
        }

        @include m.tablet {
            width: 50%;
        }

        @include m.desktop {
            width: 30%;
        }

        @include m.hd {
            width: 30%;
        }

        &::placeholder {
            color: v.$blue;
            opacity: 70%;
        }

    }
}


.modal {
    display: none;
    /* Hidden by default */
    position: fixed;
    /* Stay in place */
    z-index: 1;
    /* Sit on top */
    left: 0;
    top: 0;
    width: 100%;
    /* Full width */
    height: 100%;
    /* Full height */
    overflow: auto;
    /* Enable scroll if needed */
    background-color: rgb(0, 0, 0);
    /* Fallback color */
    background-color: rgba(0, 0, 0, 0.4);
    /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
    background-color: #fff;
    margin: 50% auto;
    /* 15% from the top and centered */
    padding: 20px;
    width: 80%;
    height: 50%;
    border-radius: 1.5rem;
    /* Could be more or less, depending on screen size */
    display: flex;
    flex-direction: column;


    @include m.phone {
        width: 80%;
        height: 50%;
    }

    @include m.tablet {
        width: 50%;
        height: 40%;
    }

    @include m.desktop {
        width: 50%;
        height: 50%;
        margin: 15% auto;
    }

    @include m.hd {
        width: 35%;
        height: 35%;

    }

    @include m.forK {
        width: 25%;
        height: 25%;
    }
}

.modal-content p {
    color: v.$black;
    text-align: center;
    padding: 4rem 2rem;
}

.modal-content p::before {
    content: url("/build/img/wrong.svg");
    width: 3rem;
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    padding-bottom: 3rem;


    @include m.phone {
        padding-top: 4rem;
    }

    @include m.tablet {
        padding-top: 2rem;
    }

    @include m.desktop {
        padding-top: 5rem;
    }

    @include m.hd {
        padding-top: 4rem;
    }

    @include m.forK {
        padding-top: 6rem;
    }

}

/* The Close Button */
.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.close:hover,
.close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}