@use '../base/variables' as v;
@use '../base/mixins' as m;


.welcome {
    &__logo {
        margin: 0 auto;
        padding: 5rem 0;
    }


    &__header {
        text-align: center;
        color: v.$blue;
        font-weight: 100;
        padding: 8rem 0 6rem 0;

        @include m.tablet {
            padding: 15rem 0 6rem 0;
        }

        @include m.desktop {
            padding: 10rem 0 6rem 0;
        }

        @include m.hd {
            padding: 20rem 0 6rem 0;
        }
    }


    &__grid {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: inherit;

    }


    &__button-login {
        background-color: v.$secondary;
        padding: 1.3rem 0;
        border-radius: 1rem;
        width: 70%;
        border: none;
        margin-bottom: 2rem;
        border: none;
        cursor: pointer;

        a {
            color: v.$black;
        }

        @include m.tablet {
            width: 50%;
        }

        @include m.desktop {
            width: 30%;
        }
    }



    &__button-register {
        background-color: v.$primary;
        color: v.$black;
        padding: 1.3rem 0;
        border-radius: 1rem;
        border: none;
        width: 70%;
        cursor: pointer;

        a {
            color: v.$white;
        }

        @include m.tablet {
            width: 50%;
        }

        @include m.desktop {
            width: 30%;
        }
    }
}