@use '../base/variables' as v;
@use '../base/mixins' as m;



#menu-hamburguesa {
    // background-image: linear-gradient(to top, v.$secondary 0%, v.$primary 100%);
    display: flex;
    justify-content: flex-start;

    @include m.tablet {
        display: none;
        justify-content: flex-end;
        width: 100%;
        text-align: center;
    }
}

#menu-hamburguesa ul li {
    padding: 1.5rem 0;
}

#menu-hamburguesa ul li a {
    color: v.$black;
    font-size: 1.8rem;
    font-family: sans-serif;
}

li {
    list-style-type: none;
    text-align: left;


}

#boton-hamburguesa {
    background-color: inherit;
    color: v.$black;
    font-size: 4rem;
    font-family: sans-serif;
    border: none;
    cursor: pointer;
    display: flex;
    margin-inline-start: auto;


}




/* Transición suave al desplegar el menú */
#menu-hamburguesa {
    /* ...otros estilos... */
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease-in-out;
}

#menu-hamburguesa.mostrando {
    max-height: 50rem;
    width: 100%;
    /* Ajustar este valor según la altura máxima del menú */
}



/* Rotar el icono de la hamburguesa al abrir el menú */
#boton-hamburguesa span.rotar {
    transform: rotate(90deg);
}



/* Estilos del menú horizontal para pantallas grandes */
@include m.tablet {
    #boton-hamburguesa {
        display: none;
    }

    #menu-hamburguesa {
        display: block;
        max-height: initial;
        overflow: initial;
    }

    #menu-hamburguesa ul {
        display: flex;
        justify-content: right;
        margin: 0;
        align-items: center;
    }

    #menu-hamburguesa:first-of-type {
        justify-content: flex-end;
    }

    #menu-hamburguesa ul li {
        padding: 3rem 2rem;
    }

    #boton-hamburguesa {

        background-image: linear-gradient(to top, v.$secondary 0%, v.$primary 100%);
        color: v.$white;
        font-size: 2.4re;
        font-family: sans-serif;

    }
}



.menu {


    &__grid {
        background-image: linear-gradient(to top, v.$secondary 0%, v.$primary 100%);

        @include m.tablet {

            display: flex;
        }
    }

    &__imagen {
        width: 5rem;
        height: 5rem;
        margin-block-start: auto;

        @include m.tablet {
            width: 6rem;
            height: 6rem;
            margin-inline-start: auto;
            cursor: pointer;
        }
    }



    &__logox2 {
        display: none;



        @include m.tablet {
            display: flex;
            width: 25%;
            padding: auto auto auto 2rem;
            margin-left: 2rem;

        }

        @include m.desktop {
            width: 15%;

        }

        @include m.hd {
            width: 8%;

        }
    }
}



.ingredients {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    align-items: flex-start;

    &__header {
        color: v.$orange;
        font-size: 2.2rem;
        padding: 6rem;
        font-weight: 200;
        text-align: center;
    }


    &__input {
        padding: 1.3rem 0 1.3rem 1rem;
        border-radius: 0.8rem;
        width: 100%;
        font-size: 1.4rem;
        font-weight: 100;
        border: 1px solid v.$black;


        &::placeholder {
            color: v.$blue;
            opacity: 70%;
        }

    }


    &__img {
        padding: 3rem 0 0 0;

    }


    &__button {
        background-color: v.$pink;
        color: v.$white;
        padding: 1.3rem 0;
        border-radius: 1rem;
        width: 70%;
        border: none;
        cursor: pointer;
        margin: 0 auto 3rem auto;

        @include m.tablet {
            width: 40%;
            margin-top: 5rem;
        }

        @include m.desktop {
            width: 25%;
            margin-inline-end: inherit;
            margin-right: 3rem;
            margin-top: 4rem;
        }

        @include m.hd {
            width: 16%;
            margin-top: 16rem;
        }
    }
}



.elections {

    &__header {
        color: v.$orange;
        font-size: 1.8rem;
        font-weight: 200;
        margin-top: 8rem;
    }


    &__grid {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-bottom: 8rem;
    }


    &__img {
        padding: 3rem 0;

    }
}