@use '../base/variables' as v;
@use '../base/mixins' as m;



#menu-hamburguesa {
    // background-image: linear-gradient(to top, v.$secondary 0%, v.$primary 100%);
    display: flex;
    justify-content: flex-start;

    @include m.tablet {
        display: none;
        justify-content: flex-end;
        width: 100%;
        text-align: center;
    }
}

#menu-hamburguesa ul li {
    padding: 1.5rem 0;
}

#menu-hamburguesa ul li a {
    color: v.$white;
    font-size: 1.8rem;
    font-family: sans-serif;

}

li {
    list-style-type: none;
    text-align: left;
}

#boton-hamburguesa {
    background-color: inherit;
    color: v.$white;
    font-size: 4rem;
    font-family: sans-serif;
    border: none;
    cursor: pointer;
    display: flex;
    margin-inline-start: auto;


}




/* Transición suave al desplegar el menú */
#menu-hamburguesa {
    /* ...otros estilos... */
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease-in-out;
}

#menu-hamburguesa.mostrando {
    max-height: 50rem;
    width: 100%;
    /* Ajustar este valor según la altura máxima del menú */
}



/* Rotar el icono de la hamburguesa al abrir el menú */
#boton-hamburguesa span.rotar {
    transform: rotate(90deg);
}



/* Estilos del menú horizontal para pantallas grandes */
@include m.tablet {
    #boton-hamburguesa {
        display: none;
    }

    #menu-hamburguesa {
        display: block;
        max-height: initial;
        overflow: initial;
    }

    #menu-hamburguesa ul {
        display: flex;
        justify-content: right;
        margin: 0;
        align-items: center;
    }

    #menu-hamburguesa:first-of-type {
        justify-content: flex-end;
    }

    #menu-hamburguesa ul li {
        padding: 3rem 2rem;
    }

    #boton-hamburguesa {

        background-image: linear-gradient(to top, v.$secondary 0%, v.$primary 100%);
        color: v.$white;
        font-size: 2.4re;
        font-family: sans-serif;

    }
}



.menu {


    &__grid {
        background-image: linear-gradient(to top, v.$secondary 0%, v.$primary 100%);

        @include m.tablet {

            display: flex;
        }
    }

    &__imagen {
        width: 5rem;
        height: 5rem;
        margin-block-start: auto;

        @include m.tablet {
            width: 6rem;
            height: 6rem;
            margin-inline-start: auto;
            cursor: pointer;
        }
    }



    &__logox2 {
        display: none;



        @include m.tablet {
            display: flex;
            width: 25%;
            padding: auto auto auto 2rem;
            margin-left: 2rem;

        }

        @include m.desktop {
            width: 15%;

        }

        @include m.hd {
            width: 8%;

        }
    }
}



.recipes {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    align-items: flex-start;


    &__back {
        display: flex;
        padding-left: 1.8rem;
        padding-top: 3rem;
    }

    &__svg {
        color: v.$primary;
        padding-right: 0.5rem;
        padding-top: 0.2rem;


    }

    svg {
        @include m.desktop {
            width: 1.8rem;
            height: 1.8rem;
        }
    }

    &__link {
        color: v.$primary;

        @include m.desktop {
            font-size: 1.8rem;
        }
    }

    &__header {
        color: v.$orange;
        font-size: 2.2rem;
        padding: 6rem;
        font-weight: 200;
        margin: 0 auto;
        text-align: center;
    }


    &__input {
        padding: 1.3rem 0 1.3rem 1rem;
        border-radius: 0.8rem;
        width: 100%;
        font-size: 1.4rem;
        font-weight: 100;
        border: 1px solid v.$black;


        &::placeholder {
            color: v.$blue;
            opacity: 70%;
        }

    }


    &__img {
        padding: 3rem 0 0 0;

    }



}


.recipe {
    background-color: v.$white;
    box-shadow: 0px 0px 15px 3px #c2c2c2;
    border-radius: 1.5rem;

    &__button {
        background-color: v.$primary;
        color: v.$white;
        padding: 1.3rem 3rem;
        border-radius: 1rem;
        border: none;
        cursor: pointer;
        text-align: center;
        margin: 2rem 0;
        width: 85%;

        @include m.tablet {
            width: 60%;
        }

        @include m.desktop {
            margin: 2rem 1rem;
        }
    }


    &__grid {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        @include m.desktop {
            flex-direction: row;
            padding: 1.5rem;
        }
    }



    &__header {
        color: v.$orange;
        font-size: 2.2rem;
        font-weight: 300;
        text-align: left;
        padding: 1.5rem 0;

        @include m.desktop {
            padding: 2rem;
        }
    }

    &__time {
        color: v.$blue;
        font-size: 1.6rem;
        padding-bottom: 1.5rem;
        margin: 0;
        display: flex;

        @include m.desktop {
            padding: 0 0 2rem 2rem;
        }


        &::before {
            content: url("../../src/img/time.svg");
            display: flex;
            width: 2.3rem;
            height: 0;
        }
    }


}



.ad1 {
    position: absolute;
    bottom: 0;


    img {
        width: 80%;

        @include m.tablet {
            width: 55%;
        }

        @include m.desktop {
            width: 100%;
        }

        @include m.hd {
            width: 70%;
        }
    }



    @include m.desktop {
        width: 25%;
        right: 0;

    }
}